<template>
  <div>
    <el-card class="box-card">
      <div class="">
        <el-form
          class=""
          style="padding: 0 60px 0 20px; width: 650px"
          ref="boxFormRef"
          :model="boxForm"
          :rules="boxFormRules"
          label-width="100px"
        >
          <el-form-item label="起始值" prop="box_code">
            <el-input v-model="boxForm.box_code"></el-input>
          </el-form-item>
        </el-form>

        <div>
          <el-button @click="reset_boxForm">重 置</el-button>
          <!-- <el-button type="primary" @click="save_boxForm">打 印</el-button> -->
           <el-button type="primary" @click="save_matterFormTest"
            >打 印</el-button
          >
        </div>
      </div>
    </el-card>
  </div>
</template>
<script>
import { formatDate } from "@/assets/libs/unit";

export default {
  data() {
    return {
      boxForm: {
        box_code: formatDate(new Date(), "yyyyMMdd") + "-",
      },
      boxFormBase: {
        box_code: formatDate(new Date(), "yyyyMMdd") + "-",
      },
      boxFormRules: {},
    };
  },
  methods: {
    reset_boxForm() {
      this.boxForm = JSON.parse(JSON.stringify(this.boxFormBase));
    },
    save_boxForm() {
      console.log("this.boxForm", this.boxForm);
    },
    
    save_matterFormTest() {
      let zpl = `
^XA

^PW1300
^LL0827

^LH0,60

^FO480,50
^AS,30,30
^FDFlex Part Number^FS
^FO100,90
^BY3
^BCN,100,Y,N,N
^FDSUB-ASSHEUH-2A001364-100^FS

^FO190,270
^AS,30,30
^FDVendor Code^FS
^FO60,310
^BCN,100,Y,N,N
^FDSPR001300^FS

^FO545,270
^AS,30,30
^FDPart QTY/P^FS
^FO505,310
^BCN,100,Y,N,N
^FD  20^FS

^FO825,270
^AS,30,30
^FDPackage Sequence^FS
^FO780,310
^BCN,100,Y,N,N
^FD  20-20^FS

^FO315,480
^AS,30,30
^FDPart Revision^FS
^FO240,520
^BCN,100,Y,N,N
^FD00A01^FS

^FO680,480
^AS,30,30
^FDManufacturing Date^FS
^FO623,520
^BCN,100,Y,N,N
^FD20210812^FS

^XZ
`;
      var printWindow = window.open();
      printWindow.document.open('text/plain')
      printWindow.document.write(zpl);
      printWindow.document.close();
      printWindow.focus();
      printWindow.print();
      printWindow.close();
