<template>
  <div>
    <el-card class="box-card">
      <div class="">
        <el-form
          class=""
          style="padding: 0 60px 0 20px; width: 650px"
          ref="deliverFormRef"
          :model="deliverForm"
          :rules="deliverFormRules"
          label-width="150px"
        >
          <el-form-item label="Flex料号" prop="Itemcode_C_Shipping">
            <el-input v-model="deliverForm.Itemcode_C_Shipping"></el-input>
          </el-form-item>
          <el-form-item label="供应商编码" prop="Supplier">
            <el-input v-model="deliverForm.Supplier"></el-input>
          </el-form-item>
          <el-form-item label="包装数量" prop="Package_Qty">
            <el-input v-model="deliverForm.Package_Qty"></el-input>
          </el-form-item>
          <el-form-item label="发货总箱数" prop="No_Ship">
            <el-input v-model="deliverForm.No_Ship"></el-input>
          </el-form-item>
          <el-form-item label="版本" prop="Rv">
            <el-input v-model="deliverForm.Rv"></el-input>
          </el-form-item>
        </el-form>
        <div>
          <el-button @click="reset_deliverForm">重 置</el-button>
          <el-button type="primary" @click="save_deliverForm">打 印</el-button>
        </div>
      </div>
    </el-card>
  </div>
</template>
<script>
import { baseManagement } from "@/request/api";
import ELTable from "@/components/formTablePage/ElTable";
import { formatDate } from "@/assets/libs/unit";
export default {
  data() {
    return {
      deliverForm: {
        Itemcode_C_Shipping: "",
        Supplier: "",
        Package_Qty: "",
        No_Ship: "",
        Rv: "",
      },
      deliverFormBase: {
        Itemcode_C_Shipping: "",
        Supplier: "",
        Package_Qty: "",
        No_Ship: "",
        Rv: "",
      },
      deliverFormRules: {},
    };
  },
  methods: {
    // 打印参数列表 查询
    getLogisticOperationSystem() {
      baseManagement
        .getLogisticOperationSystem({
          page: 1,
          page_size: 10,
        })
        .then((res) => {
          console.log("打印参数列表 查询", res);
          if (res.code == 0) {
            let rowData = res.data.data[0];
            console.log("rowData rowData rowData", rowData);
            this.deliverForm.Itemcode_C_Shipping = rowData.Itemcode_C_Shipping;
            this.deliverForm.Supplier = rowData.Supplier;
            this.deliverForm.Package_Qty = rowData.Package_Qty;
            this.deliverForm.No_Ship = rowData.No_Ship;
            this.deliverForm.Rv = rowData.Rv;

            // this.Package_Qty = res.data.data[0].Package_Qty;
            // this.Shipping_SN_length = res.data.data[0].Shipping_SN_length;
            // this.Product_length = res.data.data[0].Product_length;
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },

    reset_deliverForm() {
      this.deliverForm = JSON.parse(JSON.stringify(this.deliverFormBase));
    },
    save_deliverForm() {
      console.log("this.deliverForm", this.deliverForm);
    },
  },
  mounted() {
    // 打印参数列表 查询
    this.getLogisticOperationSystem();
  },
};
</script>

