      //     this.matterForm.initial_value = 1;
      //   }

      //   if (
      //     (this.matterForm.initial_value + "").length <
      //     this.matterForm.max_digit
      //   ) {
      //     for (
      //       let k = 0;
      //       k <
      //       this.matterForm.max_digit -
      //         (this.matterForm.initial_value + "").length;
      //       k++
      //     ) {
      //       prefix0 = prefix0 + "0";
      //     }
      //   }
      //   prefix0Val = prefix0 + this.matterForm.initial_value++;
      //   console.log("prefix0Val", prefix0Val);

      // }
      let params = {
        fileType: "matter_code",
        stem1: "SUB-ASSHEUH-2A001364-100",
        stem2: "SPR001300",
        stem3: "40",
        stem4: "20",
        stem5: "00A01",
