      let zpl = `
^XA

^PW1300
^LL0827

^LH0,60

^FO480,50
^AS,30,30
^FDFlex Part Number^FS
^FO65,90
^BY3
^BCN,100,Y,N,N
^FDSUB-ASSHEUH-2A001364-100^FS

^FO150,270
^AS,30,30
^FDVendor Code^FS
^FO20,310
^BCN,100,Y,N,N
^FDSPR001300^FS

^FO525,270
^AS,30,30
^FDPart QTY/P^FS
^FO465,310
^BCN,100,Y,N,N
^FD  20^FS

^FO815,270
^AS,30,30
^FDPackage Sequence^FS
^FO740,310
^BCN,100,Y,N,N
