^FO275,480
^AS,30,30
^FDPart Revision^FS
^FO200,520
^BCN,100,Y,N,N
^FD00A01^FS

^FO640,480
^AS,30,30
^FDManufacturing Date^FS
^FO583,520
^BCN,100,Y,N,N
^FD20210812^FS

^XZ
`;
