    getLogisticPrintApai(params) {
      baseManagement.getLogisticPrintApai(params).then((res) => {
        console.log("打印 新增", res);
        if (res.code == 0) {
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
